import { Component, OnInit } from '@angular/core';
import { Posting } from '../models/posting.model';
import { HttpClient } from '@angular/common/http';
import { NgbModule, NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

import { SignupPostingsService } from './signup-postings.service';
import { PostingModalContentComponent } from '../posting-modal-content/posting-modal-content.component';
import { PostingModalAccordianContentComponent } from '../posting-modal-accordian-content/posting-modal-accordian-content.component';

@Component({
  selector: 'signup-postings',
  templateUrl: './signup-postings.component.html',
  styleUrls: ['./signup-postings.component.css']
})
export class SignupPostingsComponent implements OnInit {

  Postings: Posting[] = [];

  constructor(private _service: SignupPostingsService,
              private modalService: NgbModal) {
  }

  ngOnInit() {
    this._service.getPostings().subscribe(postings => {
      this.Postings = postings;
      console.log(this.Postings)
    });
  }

  onItemClick(event: Event, posting: Posting) {
    const modalRef = this.modalService.open(PostingModalContentComponent, {size: 'lg'});
    modalRef.componentInstance.CurrentPosting = posting;
  }

  onAccordianItemClick(event: Event, posting: Posting) {
    const modalRef = this.modalService.open(PostingModalAccordianContentComponent, {size: 'lg'});
    modalRef.componentInstance.CurrentPosting = posting;
  }

}
