import { Component, OnInit } from '@angular/core';
import { NgbDateStruct } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'signup-datepicker',
  templateUrl: './signup-datepicker.component.html',
  styleUrls: ['./signup-datepicker.component.css']
})
export class SignupDatepickerComponent implements OnInit {

  model: NgbDateStruct;
  date: {year: number, month: number};
  
  constructor() { }

  ngOnInit() {
    this.selectToday();
  }

  selectToday() {
    const now = new Date();
    this.model = { month: now.getMonth() + 1, day: now.getDate(), year: now.getFullYear()};
  }

}
